import React from "react";
import { useState } from "react";
import axios from "axios";
import './styles.css'

const ContactForm = () => {
  const [msg, setMsg] = useState({
    name: "",
    email: "",
    text: "",
    disabled: false,
    emailSent: false,
  });

  const handleChange = (e) => {
    setMsg({
      ...msg,
      [e.target.name]: e.target.value,
    });
  };
  const handleSubmit = (e) => {
    e.preventDefault();

    setMsg({
      ...msg,
      disabled: true,
    });

    axios
      .post("http://localhost:5000/route1", msg)
      .then((res) => {
        if (res.data.success) {
          setMsg({ ...msg, disabled: false, emailSent: true });
          // might make function that lets you know you sent the message. and also clears out the form.
        } else {
          // make error function.
          setMsg({ ...msg, disabled: false, sentEmail: false });
        }
      })
      .catch((err) => console.log(err));

    
  };

  console.log(msg);

  return (
    <div>
      <h1>This is where you can send me an email.</h1>
      <form onSubmit={handleSubmit} className="contact-form">
      <label>Name</label>
      <input type="text" name="name" onChange={handleChange} value={msg.name} />
      <label>Email</label>
      <input
        type="text"
        name="email"
        onChange={handleChange}
        value={msg.email}
      />
      <label>Comment</label>
      <input type="text" name="text" onChange={handleChange} value={msg.text} />
      <button type="submit">Submit</button>
    </form> 
    </div>
   
  );
};

export default ContactForm;
